.section-name {
  padding: 3rem 0;
  margin: 0;
  text-align: center;
  font-size: 40px;
  color: #333;
  font-weight: 700; }

.grid {
  margin: 0 auto;
  padding: 1em 0 4em;
  text-align: center;
  list-style: none;
  display: flex;
  width: 1500px;
  justify-content: center;
  flex-wrap: wrap; }
  .grid figure {
    position: relative;
    overflow: hidden;
    margin: 5px;
    display: inline-block;
    min-width: 320px;
    max-width: 480px;
    max-height: 360px;
    cursor: pointer;
    background: linear-gradient(135deg, #a9e4f7 0%, #0fb4e7 100%); }
    .grid figure img {
      position: relative;
      display: block;
      max-width: 100%;
      min-height: 100%;
      opacity: 0.8;
      transform: scale(1.06);
      transition: .35s; }
    .grid figure figcaption {
      color: white;
      text-transform: uppercase;
      backface-visibility: hidden; }
      .grid figure figcaption ::after, .grid figure figcaption ::before {
        pointer-events: none; }
    .grid figure figcaption, .grid figure figcaption > a {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }
    .grid figure figcaption > a {
      z-index: 1000;
      text-indent: 200%;
      white-space: nowrap;
      font-size: 0;
      opacity: 0; }
    .grid figure p {
      margin: 0 auto;
      letter-spacing: 1px;
      font-size: 90%;
      max-width: 80%;
      transform: scale(1.5); }
    .grid figure h2 {
      margin: 0;
      word-spacing: -0.15em;
      font-weight: 800;
      padding: 20% 0 20px 0; }

.effect-chico:hover figcaption::before {
  transform: scale(1);
  opacity: 1; }

.effect-chico:hover p {
  opacity: 1;
  transform: scale(1);
  transition: .35s; }

.effect-chico:hover img {
  opacity: .5;
  transform: scale(1); }

figcaption::before {
  position: absolute;
  top: 30px;
  right: 30px;
  left: 30px;
  bottom: 30px;
  border: 1px solid white;
  content: '';
  transform: scale(1.1);
  opacity: 0;
  transition: .35s; }

p {
  opacity: 0;
  transition: .35s; }

@media screen and (max-width: 1560px) {
  figure {
    max-width: 360px; } }
