header {
  background-color: rgba(0, 0, 0, 0.8); }
  header nav {
    height: 50px;
    display: flex;
    justify-content: flex-end;
    max-width: 1600px;
    margin: auto; }
    header nav a {
      line-height: 25px;
      margin: 12px;
      font-size: 18px;
      text-decoration: none;
      font-weight: 700;
      color: rgba(255, 255, 255, 0.6);
      transition: color 0.4s; }
      header nav a:hover {
        color: white; }

.hero {
  min-height: 450px;
  background-image: url(../img/heroes/index-hero.jpg);
  position: relative;
  background-position: center;
  background-size: cover; }

.name {
  text-align: center;
  font-family: 'Caveat Brush';
  font-size: 72px;
  font-weight: normal;
  color: white;
  letter-spacing: 3px;
  margin: 0px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
